@use '@angular/material' as mat;

@include mat.all-component-typographies();
@include mat.core();

//  
$cargodrom-frontend-primary: mat.define-palette(mat.$indigo-palette);
$cargodrom-frontend-accent: mat.define-palette(mat.$pink-palette, A200, A100, A400);
$cargodrom-frontend-warn: mat.define-palette(mat.$red-palette);

//  
$cargodrom-frontend-theme: mat.define-light-theme((
  color: (
    primary: $cargodrom-frontend-primary,
    accent: $cargodrom-frontend-accent,
    warn: $cargodrom-frontend-warn,
  )
));

//      
@include mat.all-component-themes($cargodrom-frontend-theme);

//   
@import url('./app/styles/cargodrom.css');
@import url('./app/styles/material.css');
@import url('./app/styles/btn.css');
@import url('./app/styles/table.css');
@import url('./app/styles/input.css');
@import url('./app/styles/form.css');
@import url('./app/styles/const.css');
@import url('./app/styles/icon.css');
// libs
// @import "~@ng-select/ng-select/themes/material.theme.css";
@import "~@ng-select/ng-select/themes/default.theme.css";



html, body { height: 100%; }
body {
  margin: 0;

}





/*    Chrome, Safari, Edge, Opera */
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none; margin: 0;
}
/*    Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}

/*    Firefox */
@-moz-document url-prefix() {
}






