html, .content{
  --accent:var(--user-brend_accent, #db563b);
  --accent_text:var(--user-brend_accent_text, #FFFFFF);
  --background:var(--user-brend_background,#F7F9FC);
  --background2:var(--user-brend_background2,#FFFFFF);
  --background3: var(--user-brend_background3,#83909e);
  --event_negative:var(--user-brend_event_negative ,#fd7171) ;
  --event_negative_text:var(--user-brend_event_negative_text ,#FFFFFF);
  --event_neutral:var(--user-brend_event_neutral,#aab7c5);
  --event_neutral_text:var(--user-brend_event_neutral_text,#FFFFFF);
  --event_positive:var(--user-brend_event_positive, #81dd7c);
  --event_positive_text:var(--user-brend_event_positive_text, #FFFFFF);
  --header_menu: var(--user-brend_header_menu,#4B66AD);
  --header_menu_text: var(--user-brend_header_menu_text,#FFFFFF);
  --line: var(--user-brend_line,#E0E5EB);
  --text: var(--user-brend_text,#212121);
  --text2: var(--user-brend_text2,#606a74);
  --text3: var(--user-brend_text3,#FFFFFF);
}
