@font-face {
  font-family: 'regular';
  src: url('../../assets/lato-regular.eot');
  src: url('../../assets/lato-regular.eot?#iefix') format('embedded-opentype'),
  url('../../assets/lato-regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'regular';
  src: url('../../assets/lato-bold.eot');
  src: url('../../assets/lato-bold.eot?#iefix') format('embedded-opentype'),
  url('../../assets/lato-bold.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;
}



/* html * {max-height: 1000000px; box-sizing: border-box } */
/* html { height: 100%; padding: 0; margin: 0; font-family: 'regular', 'arial', 'sans-serif'; min-width: 1600px; } */
html { height: 100%; padding: 0; margin: 0; font-family: 'regular', 'arial', 'sans-serif'; min-width: 1455px;}
body { height: 100%; padding: 0; margin: 0; background: #f7f9fc; }
.content{color: var(--text); background-color: var(--background); }

input, textarea, a:focus{outline-width:0;outline:none;}
input[type="submit"], input[type="button"] , input[type="checkbox"]{  cursor: pointer; -webkit-appearance: none; -moz-appearance: none; -o-appearance: none; appearance: none; }
input[type="checkbox"], input[type="radio"], label { vertical-align: middle; }

.btn { background: var(--accent, #db563b); color: #fff; padding: 20px 16px; font-size: 15px; line-height: 17px; text-align: center; border-radius: 6px; display: inline-block; cursor: pointer; }


.content {height: 100%;  }
app-pages{ .content {min-height: 100%; height: auto; }}
.user-module { height: 100%; display: flex; flex-direction: column;  }

.auth-bg { background: #44f url('../../assets/auth-bg.png') no-repeat 50% 50%; background-size: cover;; display: flex; justify-content: center; align-items: center; height: 100%; }
.auth-form { width: 560px; height: auto; background: #fff; position: absolute; }
.auth-form .hdr { border-bottom: 1px solid #E0E5EB; padding: 27px 32px 24px; display: flex; align-items: center; }
.auth-form .logo { display: inline-block; background: url('../../assets/logo-full.svg') no-repeat 0 0; width: 168px; height: 44px; }
.auth-form .sep { flex-grow: 1; }
.auth-form .fm-body { padding: 32px 0 15px; }
.auth-form .form-row { padding: 0 32px 16px; display: flex; align-items: flex-end;  font-size: 13px; line-height: 17px;  }
.auth-form .form-row.sp { padding-bottom: 24px; }
.auth-form .form-row > * { width: 100%; }
.auth-form .form-row.inline > * { width: 50%; }
.auth-form .form-row.inline > *:first-child { margin-right: 16px; }
.auth-form input[type="text"], .auth-form input[type="password"] { background: #F7F9FC; border: 1px solid #e0e5eb; font-size: 15px; line-height: 17px; padding: 10px 35px 10px 20px; border-radius: 6px; box-sizing: border-box; width: 100%; }
.auth-form .form-label { font-size: 10px; line-height: 12px; padding: 0; text-transform: uppercase; color: #606A74; }
.auth-form .form-input { margin-top: 6px; }
.auth-form .btn { display: block; padding: 13px 0 10px; cursor: pointer; }
.auth-form .btn.alt { background: var(--header_menu, #4B66AD); }
.auth-form .form-hr { margin-top: 15px; margin-bottom: 32px; }
.auth-form .desc { color: #606A74; }
.auth-form .forgot { color: var(--accent,  #DB563B); }
.auth-form .forgot > span { cursor: pointer; }
.auth-form .back { font-size: 24px; line-height: 24px; font-weight: bold; background: url('../../assets/pic-backto2.svg') no-repeat 0 0; padding-left: 27px; cursor: pointer; }
.form-hr { border-top: 1px solid #E0E5EB; }

.auth-form .form-row.form-error { color: #ff0000; font-weight: 600; }
.auth-form .btn::before { display: none; }

.header { box-shadow: 0 1px 6px rgba(0,0,0,0.08); z-index: 1; position: relative; }

.main-menu { display: flex; background: var(--header_menu, #4B66AD); color: var(--header_menu_text, #FFFFFF); height: 56px; box-shadow: 0 4px 5px rgba(0,0,0,0.1); }
.main-menu > * { min-width: 56px; display: flex;  }
.main-menu > * > span { margin: auto; }

.main-menu .logo {
  background: var(--logo_url, url('../../assets/logo-mini-white.svg')) no-repeat 50% 50%;
  background-size: var(--bg_size, auto);
}
/* .main-menu .logo { background: url('../../assets/logo-mini-white.svg') no-repeat 50% 50%; } */
.main-menu .item { font-size: 15px; padding: 0 24px; display: flex; align-items: center; cursor: pointer; box-shadow: inset 1px 0 0 color-mix(in srgb, var(--header_menu_text, #FFFFFF) 17%, transparent); text-decoration: none; color: var(--header_menu_text); flex-shrink: 0; }
@media (max-width: 1750px) {
  .main-menu .item {padding: 0 12px; font-size: 12px;}
}
.main-menu .item svg { margin-right: 10px; margin-top: -2px; opacity: 0.6;}
/* .main-menu .item svg path { fill: #B4C3EB; } */
.main-menu .item svg path { fill: var(--header_menu_text, #FFFFFF); opacity: 0.9; }
/* .main-menu .item:hover { background: #5D76B9; } */ .main-menu .item:hover { opacity: 0.6; }
/* .main-menu .item.sel { background: #fff; color: var(--header_menu, #4B66AD);  }*/
.main-menu .item.sel { background: var(--accent_text, #fff); color: var(--header_menu, #4B66AD);}
.main-menu .item.sel:hover { background: #F1F5FF; }
.main-menu .item.sel svg path { fill: var(--header_menu, #4B66AD); }
/* .main-menu .item.dashboard { } */
.main-menu .item.request svg { margin-top: 5px; margin-right: 5px; }
