/* .main-menu .item.order {} */
/* .main-menu .item.tariff {} */
/* .main-menu .item.contracor {} */
/* .main-menu .item.report {} */
/* .main-menu .item.client {} */
/* .main-menu .item.guide {} */

.main-menu .div { flex-grow: 1; }

.main-menu .currency { font-size: 13px; min-width: 71px; }
.main-menu .currency > span > * { display: block; text-align: left; }
.main-menu .currency > span :first-child { font-size: 10px; color: var(--header_menu_text);opacity: 0.6; margin-bottom: 4px; }
/* .main-menu .currency > span :last-child {} */

.main-menu .fn { box-shadow: inset 1px 0 0 color-mix(in srgb, var(--header_menu_text, #FFFFFF) 17%, transparent); cursor: pointer; display: flex; align-items: center; }
/* .main-menu .fn:hover { background: #5D76B9; } */ .main-menu .fn:hover { opacity: 0.6; }
.main-menu .fn.sel { background: #fff; color: var(--header_menu, #4B66AD);  }
/* .main-menu .fn.sel:hover { background: #F1F5FF; } */
.main-menu .fn.sel svg path { fill: var(--header_menu); }
.main-menu .fn svg { margin: 4px auto 0; }
.main-menu .fn svg path { fill: var(--header_menu_text, #FFFFFF) }
.main-menu .fn.msg { position: relative; }
.main-menu .fn.msg span span { position: absolute; left: 32px; top: 32px; display: inline-block; background: #fff; color: var(--header_menu, #4B66AD); border-radius: 50%; font-size: 10px; font-weight: bold; min-width: 14px; padding: 1px 0; text-align: center; }
.main-menu .fn.msg span span:empty { display: none; }
