.subheader { border-bottom: 1px solid var(--header_menu); padding: 15px 12px 12px 24px; background-color: var(--background2);}
.subheader .placer { display: flex; align-items: center; min-height: 43px; box-sizing: border-box; width: 100%;}

.subheader .div { flex-grow: 1; }
.subheader .title { font-size: 26px; line-height: 30px; color: var(--header_menu); padding-right: 24px;  font-weight: bold; text-decoration: none; }
.subheader a.title { position: relative; padding-left: 30px; cursor: pointer; }
.subheader a.title:before { content: ""; position: absolute; left: 0; top: 0; bottom: 0; background: url('../../assets/pic-backto.svg') no-repeat 50% 50%; width: 13px; }
.subheader .title div { font-size: 13px; line-height: 13px; color: var(--text2,#606A74); font-weight: normal; margin-top: 8px; .req{color: var(--accent, #db563b);} }
.subheader .item { margin-left: 24px; font-size: 13px; line-height: 12px; color: #606A74; }
.subheader .item .val {  font-size: 15px; line-height: 17px; color: #2C3640; font-weight: bold; display: block; margin-top: 5px; }

.subheader .links { border-left: 1px solid #C3CCD6; display: flex; align-items: center; min-height: 43px; box-sizing: border-box;  }

.subheader .link { height: 25px; margin-left: 24px; font-size: 13px; line-height: 12px; color: #606A74; cursor: pointer; display: flex; align-items: center; text-decoration: none; }
.subheader .link:hover { opacity: 0.7; }
.subheader .link svg { margin: -2px 9px 0 0; }

.table-footer { display: flex; padding: 20px 20px; background-color: var(--background); }
.table-footer .limit { flex-grow: 1; display: flex; align-items: center; }
.table-footer .note { font-size: 13px; line-height: 13px; color: #606A74; margin-left: 15px; }

.table-footer select[name="limit"] { width: 170px; }
.table-footer .ui-select { display: inline-block; vertical-align: middle; width: auto; position: relative; margin-bottom: 0; text-align: left; font-size: 13px; line-height: 13px; border: none; box-sizing: border-box; }
/* .table-footer .ui-select { display: inline-block; vertical-align: middle; width: auto; position: relative; margin-bottom: 0; text-align: left; font-size: 13px; line-height: 13px; border: none; box-shadow: 0 2px 8px rgba(36,45,67,0.08); box-sizing: border-box; } */
.table-footer .ui-select-input { display: block; color: #606A74; background: #fff; padding: 15px 50px 12px 15px; box-sizing: border-box; overflow: hidden; white-space: nowrap; cursor: pointer; border-radius: 6px; }
.table-footer .invert .ui-select-input { position: relative; z-index: 10001 }
.table-footer .ui-select-input-open { border-radius: 6px 6px 0 0; }
.table-footer .invert > .ui-select-input-open { border-radius: 0 0 6px 6px; }
.table-footer .ui-select-button { display: flex; position: absolute; top: 0; right: 0; width: 40px; bottom: 0; cursor: pointer; font-size: 0; }
.table-footer .invert > .ui-select-button { z-index: 10001; }
.table-footer .ui-select-button:hover { opacity: 0.7; }
.table-footer .ui-select-button:after { content: ""; transform: rotate(90deg); margin: auto; display: inline-block; width: 6px; height: 10px; transition: 0.1s; background: url('../../assets/pic-paginator-arrow.svg') no-repeat 50% 50%; }
/* .table-footer .ui-select-button-open { } */
.table-footer .ui-select-button-open:after { transform: rotate(270deg); }
.table-footer .ui-select-fader { position: absolute; top: 0; bottom: 0; }
