.table-footer .ui-select-item.selected:hover { background: #e99786; }

.navigator { font-size: 13px; line-height: 13px; }
.navigator .item  { cursor: pointer; display: inline-block; padding: 15px 10px 12px; text-align: center; text-decoration: none; vertical-align: middle; background: #fff; color: #606A74; border-radius: 6px; box-shadow: 0 2px 8px rgba(36,45,67,0.08); min-width: 40px; box-sizing: border-box; }
.navigator .item + .item  { margin-left: 8px; }
.navigator .item.sel  { font-weight: bold; background: #83909E; color: #fff; box-shadow: inset 0 2px 1px rgba(44,54,64,0.2); }
.navigator .item.arrow { background: #fff url('../../assets/pic-paginator-arrow.svg') no-repeat 50% 50%; padding: 0; width: 40px; height: 40px; }
.navigator .item.arrow.left { transform: rotate(180deg); box-shadow: 0 -2px 8px rgba(36,45,67,0.08); }
/* .navigator .item.arrow.right {} */
.navigator .dot { margin: 0 15px; }
/* .navigator .icon-control-end, .navigator .icon-control-start { font-size: 12px; font-weight: bold; } */
.navigator .icon-control-end .n, .navigator .icon-control-start .n { display: none; }

