.switch .switch-title { cursor: pointer; font-size: 15px; line-height: 14px; vertical-align: top; margin: 2px 0 0 6px; display: inline-block; color: #606A74; }
.switch input { display: none; }
.switch .switcher { box-sizing: border-box; border: 1px solid #E0E5EB; width: 40px; height: 16px; border-radius: 50px; position: relative; display: inline-block; vertical-align: top; overflow: hidden; cursor: pointer; }
.switch .switcher .bg { display: flex; width: 200%; height: 100%; margin-left: -100%; transition: margin 0.1s ease-in 0s; text-transform: uppercase; }
.switch .switcher .bg:before,
.switch .switcher .bg:after { display: block; float: left; text-align: center; width: 50%;  padding: 0; line-height: 10px; font-size: 10px; color: white; font-weight: bold; margin: auto 0; }
.switch .switcher .bg:before { content: attr(data-on); padding-right: 21px; color: #919191; }
.switch .switcher .bg:after { content: attr(data-off); padding-left: 22px;  color: #919191;  }
.switch .switcher .thumb { background: #E0E5EB; display: block; width: 16px; right: calc(100% - 18px); margin: 0; filter: grayscale(1); opacity: 0.7; border-radius: 50px; position: absolute; top: 2px; bottom: 2px;  transition: all 0.1s ease-in 0s; }

.switch.outer .switcher .bg:before,
.switch.outer .switcher .bg:after { display: none; }
.switch.outer .switch-title:before { display: block; content: attr(data-off); }

.switch input:checked ~ .switcher  {  border-color: #C3CCD6; background: #fff; }
.switch input:checked ~ .switcher .bg { margin-left: 0; }
.switch input:checked ~ .switcher .thumb { right: 2px; filter: grayscale(0); opacity: 1; background: var(--accent,  #DB563B); }
.switch input:checked ~ .switch-title { color: #2C3640; }
.switch.outer input:checked ~.switch-title:before { content: attr(data-on); }

