.add.show.additional {
  /* min-height: 100px; */
  visibility: visible;  /*    */
  opacity: 1;           /*    */
  max-height: 400px;/*     200px */
  transform: translateY(0);
  transition: opacity 1s ease, max-height 1s ease, visibility 1s ease, transform 1s ease;
}

.add.show.additional.hidden-filter {
  /* min-height: 0; */
  visibility: hidden;   /*    */
  opacity: 0;           /*   */
  max-height: 0;         /*   0 */
  transform: translateY(-200px); /*    20px */
  transition: opacity 1s ease, max-height 1s ease, visibility 1s ease, transform 1s ease;
}


